'use client'

import { useParams } from 'next/navigation'
import { useEffect, useState } from 'react'
import {
	XAxis,
	YAxis,
	CartesianGrid,
	Tooltip,
	ResponsiveContainer,
	AreaChart,
	Area,
} from 'recharts'
import {
	ArrowLeft,
	Activity,
	AlertTriangle,
	Zap,
	ShieldCheck,
} from 'lucide-react'
import Link from 'next/link'

export default function MonitorDetailsPage() {
	const { id } = useParams()
	const [data, setData] = useState<any>(null)

	useEffect(() => {
		fetch(
			`${
				process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000'
			}/monitors/${id}`
		)
			.then((res) => res.json())
			.then(setData)
	}, [id])

	if (!data || !data.checks) {
		return (
			<div className='p-10 text-center text-slate-500 dark:text-slate-400 animate-pulse'>
				Загрузка детальной статистики...
			</div>
		)
	}

	// --- Расчёт статистики ---
	const totalChecks = data.checks.length
	const upChecks = data.checks.filter((c: any) => c.status === 'UP').length
	const uptimePercentage =
		totalChecks > 0 ? ((upChecks / totalChecks) * 100).toFixed(2) : '0.00'

	const avgLatency =
		totalChecks > 0
			? Math.round(
					data.checks.reduce(
						(acc: number, curr: any) => acc + curr.responseTime,
						0
					) / totalChecks
			  )
			: 0

	const downEvents = data.checks.filter((c: any) => c.status === 'DOWN').length

	const history = data.checks.map((c: any) => ({
		time: new Date(c.createdAt).toLocaleTimeString([], {
			hour: '2-digit',
			minute: '2-digit',
		}),
		ms: c.responseTime,
	}))

	return (
		<main className='min-h-screen bg-slate-50 dark:bg-slate-950 p-6 md:p-12'>
			<div className='max-w-6xl mx-auto'>
				{/* Back */}
				<Link
					href='/'
					className='flex items-center gap-2 text-slate-500 dark:text-slate-400 hover:text-slate-800 dark:hover:text-slate-200 transition-colors mb-6 group'
				>
					<ArrowLeft
						size={18}
						className='group-hover:-translate-x-1 transition-transform'
					/>
					Назад к дашборду
				</Link>

				{/* Header */}
				<div className='flex flex-col md:flex-row justify-between items-start md:items-end gap-4 mb-8'>
					<div>
						<h1 className='text-4xl font-black tracking-tight text-slate-900 dark:text-slate-100'>
							{data.name}
						</h1>
						<p className='text-slate-500 dark:text-slate-400 text-lg'>
							{data.url}
						</p>
					</div>

					<div
						className={`px-4 py-2 rounded-xl font-bold flex items-center gap-2 shadow-sm ${
							data.status === 'UP'
								? 'bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-400'
								: 'bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-400'
						}`}
					>
						<div
							className={`w-2 h-2 rounded-full animate-pulse ${
								data.status === 'UP'
									? 'bg-green-600 dark:bg-green-400'
									: 'bg-red-600 dark:bg-red-400'
							}`}
						/>
						{data.status}
					</div>
				</div>

				{/* Stat cards */}
				<div className='grid grid-cols-1 md:grid-cols-3 gap-6 mb-8'>
					{/* Uptime */}
					<div className='bg-white dark:bg-slate-900 p-6 rounded-3xl border border-slate-200 dark:border-slate-800 shadow-sm flex items-center gap-5'>
						<div className='p-4 bg-green-50 text-green-600 dark:bg-green-900/30 dark:text-green-400 rounded-2xl'>
							<ShieldCheck size={28} />
						</div>
						<div>
							<p className='text-xs font-bold text-slate-400 uppercase tracking-wider'>
								Доступность
							</p>
							<p className='text-3xl font-black text-slate-800 dark:text-slate-100'>
								{uptimePercentage}%
							</p>
						</div>
					</div>

					{/* Latency */}
					<div className='bg-white dark:bg-slate-900 p-6 rounded-3xl border border-slate-200 dark:border-slate-800 shadow-sm flex items-center gap-5'>
						<div className='p-4 bg-blue-50 text-blue-600 dark:bg-blue-900/30 dark:text-blue-400 rounded-2xl'>
							<Zap size={28} />
						</div>
						<div>
							<p className='text-xs font-bold text-slate-400 uppercase tracking-wider'>
								Средний ответ
							</p>
							<p className='text-3xl font-black text-slate-800 dark:text-slate-100'>
								{avgLatency}{' '}
								<span className='text-lg font-medium text-slate-400'>ms</span>
							</p>
						</div>
					</div>

					{/* Incidents */}
					<div className='bg-white dark:bg-slate-900 p-6 rounded-3xl border border-slate-200 dark:border-slate-800 shadow-sm flex items-center gap-5'>
						<div className='p-4 bg-red-50 text-red-600 dark:bg-red-900/30 dark:text-red-400 rounded-2xl'>
							<AlertTriangle size={28} />
						</div>
						<div>
							<p className='text-xs font-bold text-slate-400 uppercase tracking-wider'>
								Инциденты
							</p>
							<p className='text-3xl font-black text-slate-800 dark:text-slate-100'>
								{downEvents}
							</p>
						</div>
					</div>
				</div>

				{/* Chart */}
				<div className='bg-white dark:bg-slate-900 p-6 rounded-3xl border border-slate-200 dark:border-slate-800 shadow-sm mb-8'>
					<h2 className='text-xl font-bold mb-6 flex items-center gap-2 text-slate-800 dark:text-slate-100'>
						<Activity className='text-blue-500' /> Производительность (24ч)
					</h2>

					<div className='h-[300px] w-full'>
						<ResponsiveContainer width='100%' height='100%'>
							<AreaChart data={history}>
								<defs>
									<linearGradient id='colorMs' x1='0' y1='0' x2='0' y2='1'>
										<stop offset='5%' stopColor='#3b82f6' stopOpacity={0.25} />
										<stop offset='95%' stopColor='#3b82f6' stopOpacity={0} />
									</linearGradient>
								</defs>

								<CartesianGrid
									strokeDasharray='3 3'
									vertical={false}
									stroke='#334155'
									opacity={0.2}
								/>

								<XAxis dataKey='time' hide />
								<YAxis
									unit='ms'
									stroke='#94a3b8'
									fontSize={12}
									tickLine={false}
									axisLine={false}
								/>

								<Tooltip
									contentStyle={{
										background: '#020617',
										borderRadius: '14px',
										border: '1px solid #1e293b',
										color: '#e5e7eb',
									}}
								/>

								<Area
									type='monotone'
									dataKey='ms'
									stroke='#3b82f6'
									strokeWidth={3}
									fill='url(#colorMs)'
								/>
							</AreaChart>
						</ResponsiveContainer>
					</div>
				</div>

				{/* Table */}
				<div className='bg-white dark:bg-slate-900 rounded-3xl border border-slate-200 dark:border-slate-800 shadow-sm overflow-hidden'>
					<div className='p-6 border-b border-slate-100 dark:border-slate-800'>
						<h2 className='text-xl font-bold flex items-center gap-2 text-slate-800 dark:text-slate-100'>
							<AlertTriangle className='text-amber-500' /> Журнал сбоев
						</h2>
					</div>

					<div className='overflow-x-auto'>
						<table className='w-full text-left'>
							<thead className='bg-slate-50 dark:bg-slate-800 text-slate-500 uppercase text-[10px] font-bold tracking-widest'>
								<tr>
									<th className='px-6 py-4'>Дата и время</th>
									<th className='px-6 py-4'>Статус</th>
									<th className='px-6 py-4'>Код</th>
									<th className='px-6 py-4'>Задержка</th>
								</tr>
							</thead>
							<tbody className='divide-y divide-slate-100 dark:divide-slate-800'>
								{data.checks.filter((c: any) => c.status === 'DOWN').length ===
								0 ? (
									<tr>
										<td
											colSpan={4}
											className='px-6 py-12 text-center text-slate-400'
										>
											Сбоев не зафиксировано
										</td>
									</tr>
								) : (
									data.checks
										.filter((c: any) => c.status === 'DOWN')
										.reverse()
										.map((incident: any) => (
											<tr
												key={incident.id}
												className='hover:bg-slate-50 dark:hover:bg-slate-800 transition-colors'
											>
												<td className='px-6 py-4 text-sm text-slate-600 dark:text-slate-300'>
													{new Date(incident.createdAt).toLocaleString()}
												</td>
												<td className='px-6 py-4'>
													<span className='px-2 py-1 bg-red-100 text-red-600 dark:bg-red-900/30 dark:text-red-400 rounded-lg text-[10px] font-bold'>
														DOWN
													</span>
												</td>
												<td className='px-6 py-4 text-sm font-mono text-slate-500'>
													{incident.statusCode || 'Timeout'}
												</td>
												<td className='px-6 py-4 text-sm text-slate-500'>
													{incident.responseTime}ms
												</td>
											</tr>
										))
								)}
							</tbody>
						</table>
					</div>
				</div>
			</div>
		</main>
	)
}
